header {
  min-height: 10vh;
  margin: auto;
  background-color: #070c11;
  color: #fff;
  text-align: center;
  align-items: center;
  padding-top: 10px;
}

header h1 {
  margin: 0;
}

#logo {
  vertical-align: middle;
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

main {
  background-color: #202224;
}

#cases {
  min-height: 90vh;
  padding-bottom: 50px;
  color: rgb(177, 177, 177);
  background-color: #202224;
}

#cases h2 {
  padding: 10px;
}

#cases h4 {
  padding-top: 10px;
}

#cases h1,
h4 {
  text-align: center;
}

.cases-wrapper {
  width: 80%;
  margin: auto;
}

#PH {
  font-size: 50px;
}

.box-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

.box {
  background-color: #0f0e0e;
  height: 130px;
  width: 30%;
  margin: 15px;
  border-radius: 10px;
}

#protective {
  min-height: 90vh;
  padding-bottom: 50px;
  color: rgb(177, 177, 177);
  background-color: #202224;
  text-align: center;
}

.pmeasures {
  background-color: #0f0e0e;
  width: 70%;
  margin: auto;
  border-radius: 10px;
}

.pmeasure {
  display: flex;
  justify-content: space-around;
  padding: 20px;
  align-items: center;
  text-align: start;
}

.p-icons {
  flex: 1;
}

.p-icons img {
  display: block;
  margin: auto;
}

.p-text {
  flex: 5;
}
.p-text h4 {
  text-align: start;
}

#twitter {
  min-height: 90vh;
  padding-bottom: 50px;
  color: rgb(177, 177, 177);
  background-color: #202224;
  text-align: center;
}

.twitter-container {
  width: 80%;
  margin: auto;
}

.twitters {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
}

.twitters a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.twitter {
  border-radius: 5px;
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.5);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.5);
}

footer {
  min-height: 10vh;
  background-color: #070c11;
  color: #fff;
  text-align: center;
  padding-top: 10px;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
  font-size: 12px;
}

footer a {
  text-decoration: none;
  color: inherit;
}

footer p {
  margin: 2px;
}

@media screen and (max-width: 1024px) and (min-width: 769px) {
  .box {
    width: 45%;
  }
  .twitters {
    flex-direction: column;
    padding: 20px;
  }
  .twitter {
    margin-bottom: 20px;
  }
  footer {
    font-size: 15px;
  }
}

@media screen and (max-width: 768px) and (min-width: 200px) {
  .box {
    flex-wrap: nowrap;
    flex-direction: column;
    width: 90%;
    justify-content: center;
    margin: auto;
    margin-top: 15px;
  }
  #PH {
    font-size: 40px;
  }
  h2,
  p {
    text-align: center;
  }
  .pmeasure {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .p-text h4 {
    text-align: center;
  }
  .twitters {
    flex-direction: column;
    padding: 20px;
  }
  .twitter {
    margin-bottom: 20px;
  }
  footer {
    font-size: 10px;
  }
}
